import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getDormitoryRooms, deleteDormitoryRoom } from 'services/dormitoryRoom';
import { moneyToString } from 'utils/formatUtil';

class ListDormitoryRoomPage extends Component {
  state = {};
  handleDelete = async id => {
    await deleteDormitoryRoom({ id });
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'Tòa KTX',
        dataIndex: 'dormitoryId',
        key: 'dormitoryId',
      },
      {
        title: 'Tầng',
        dataIndex: 'dorm_FloorId',
        key: 'dorm_FloorId',
      },
      {
        title: 'Số phòng',
        dataIndex: 'roomNo',
        key: 'roomNo',
      },
      {
        title: 'Sức chứa',
        dataIndex: 'size',
        key: 'size',
      },
      {
        title: 'Thiết bị',
        dataIndex: 'device',
        key: 'device',
      },
      {
        title: 'Số tiền/người',
        dataIndex: 'feePerPerson',
        key: 'feePerPerson',
        render: text => moneyToString(text),
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#dormitory-rooms/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Phòng KTX">
        <CartHeadAction>
          <BtnAddNew href="#dormitory-rooms/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getDormitoryRooms}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListDormitoryRoomPage />
  </Page>
);



