import { request } from 'utils';

const prefix = '/api/categories';

export const getDormitoryRooms = ({
  roomNo,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/DormitoryRoom`,
    method: 'GET',
    params: { roomNo, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createDormitoryRoom = ({ dorm_FloorId, roomNo, size, device, feePerPerson } = {}) =>
  request({
    prefix,
    url: `/DormitoryRoom`,
    method: 'POST',
    params: {},
    data: { dorm_FloorId, roomNo, size, device, feePerPerson },
  });

export const getDormitoryRoomById = ({ id } = {}) =>
  request({
    prefix,
    url: `/DormitoryRoom/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const updateDormitoryRoom = ({
  id,
  dorm_FloorId,
  roomNo,
  size,
  device,
  feePerPerson,
} = {}) =>
  request({
    prefix,
    url: `/DormitoryRoom/${id}`,
    method: 'PUT',
    params: {},
    data: { id, dorm_FloorId, roomNo, size, device, feePerPerson },
  });

export const deleteDormitoryRoom = ({ id } = {}) =>
  request({
    prefix,
    url: `/DormitoryRoom/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



