import React, { Component } from 'react';
import { Table } from 'antd';

export default class TableData extends Component {
  state = {
    data: [],
    loading: false,
    pagination: {
      pageSize: 10,
    },
    page: 0,
  };

  componentDidMount() {
    this.actionGetData();
  }

  componentWillReceiveProps = nextProps => {
    if (nextProps.reload != this.props.reload) {
      this.actionGetData();
    }
    if (nextProps.paramSearch != this.props.paramSearch) {
      this.actionGetData({ ...nextProps.paramSearch });
    }
  };

  handleTableChange = (pagination, filters, sorter) => {
    this.actionGetData({}, { page: pagination.current });
  };

  actionGetData = async (paramSearch = {}, { page } = {}) => {
    this.setState({
      loading: true,
    });
    const res = await this.props.service({ page, ...paramSearch });
    const pagination = { ...this.state.pagination };
    pagination.total = res.data.totalResults;
    this.setState({
      data: res.data.items,
      loading: false,
      pagination,
    });
  };

  render() {
    return (
      <Table
        bordered
        loading={this.state.loading}
        pagination={this.state.pagination}
        onChange={this.handleTableChange}
        dataSource={this.state.data}
        columns={this.props.columns}
        {...this.props}
      />
    );
  }
}



